.NavSuper {
  display: flex;
  flex-direction: row;
  width: 100%;
  justify-content: center;
  background-color: orange;
  font-size: 1rem;
  color: black;
  text-decoration: none;
}

.NavContainer {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  width: 70%;
}

.NavRightPanel {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  width: 80%;
}

.Navbox {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;

  border: black solid 1px;
}

.Navlogo {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  width: 7%;
}

.Navegacion {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  width: 60%;
}

.Desplegable {
  display: flex;
  flex-direction: row;
  justify-content: center;
  width: 20%;
}

.MenuBtn {
  width: 150px;
  height: 55px;
  border: solid rgb(36, 36, 206) 4px;
  font-family: "Patrick Hand", cursive;
  font-weight: bolder;
  color: orange;
  background-color: rgb(36, 36, 206);
  border-radius: 2rem;
  box-shadow: inset 0 0 0 0 rgb(242, 184, 49);
  transition: ease-out 0.3s;
  font-size: 1.9rem;
  outline: none;
  cursor: pointer;
}

.MenuBtn:hover {
  color: rgb(36, 36, 206);
  background-color: orange;
  border: solid rgb(36, 36, 206) 4px;
}

.Social {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  width: 20%;
}

.NavbtnCTA {
  width: 110px;
  height: 55px;
  border: solid rgb(36, 36, 206) 4px;
  font-family: "Patrick Hand", cursive;
  font-weight: bolder;
  color: orange;
  background-color: rgb(36, 36, 206);
  border-radius: 2rem;
  box-shadow: inset 0 0 0 0 rgb(242, 184, 49);
  transition: ease-out 0.3s;
  font-size: 1.4rem;
  outline: none;
  cursor: pointer;
}

.NavbtnCTA:hover {
  color: rgb(36, 36, 206);
  background-color: orange;
  border: solid rgb(36, 36, 206) 4px;
}
